<template>
  <q-select
    outlined
    autofocus
    use-input
    emit-value
    :dense="dense"
    hide-bottom-space
    option-value="id"
    @filter="filterBrandsFn"
    v-model="selectModel"
    :options="filteredBrandsOptions"
    class="rounded text-body2 input-white"
    popup-content-class="bg-white rounded-bottom"
    input-class="text-body1 letter-space-normal text-primary"
    @input-value="(val) => (inputModel = val)"
    :rules="[(val) => (required ? !!val || '' : true)]"
  >
    <template v-slot:option="scope">
      <q-item
        clickable
        v-bind="scope.itemProps"
        :class="scope.selected ? 'bg-accent text-secondary' : 'text-primary'"
        class="hover-text-secondary hover-bg-accent"
        @click="scope.opt.action"
      >
        <q-item-section avatar style="min-width: unset">
          <q-avatar
            square
            size="md"
            color="accent"
            class="rounded"
            text-color="secondary font-medium"
          >
            <img
              v-if="scope.opt.avatar"
              :src="`${$cdn_host}/${avatars[scope.opt.avatar]?.path}`"
              :alt="
                avatars[scope.opt.avatar]?.file ||
                avatarNameAbbr(scope.opt.brand_name)
              "
              class="q-pa-xs"
            />
            <q-icon
              v-else-if="scope.opt.icon"
              size="xs"
              :name="scope.opt.icon"
            />
            <span v-else-if="scope.opt.brand_name">
              {{ avatarNameAbbr(scope.opt.brand_name) }}
            </span>
            <q-icon v-else name="icon-star-asterisk" />
          </q-avatar>
        </q-item-section>
        <q-item-section>
          <q-item-label>
            {{ scope.opt.brand_name }}
          </q-item-label>
        </q-item-section>
      </q-item>
    </template>

    <template v-slot:selected>
      <div v-if="!inputModel" class="row items-center q-col-gutter-sm">
        <div avatar v-if="selectedOrgBrands[selectModel]?.brand_name">
          <q-avatar
            square
            size="md"
            color="accent"
            class="rounded"
            text-color="secondary font-medium"
          >
            <img
              v-if="selectedOrgBrands[selectModel]?.avatar"
              :src="`${$cdn_host}/${
                avatars[selectedOrgBrands[selectModel]?.avatar]?.path
              }`"
              :alt="
                avatars[selectedOrgBrands[selectModel]?.avatar]?.file ||
                avatarNameAbbr(selectedOrgBrands[selectModel]?.brand_name)
              "
              class="q-pa-xs"
            />
            <q-icon
              v-else-if="selectedOrgBrands[selectModel]?.icon"
              size="xs"
              :name="selectedOrgBrands[selectModel]?.icon"
            />
            <span v-else-if="selectedOrgBrands[selectModel]?.brand_name">
              {{ avatarNameAbbr(selectedOrgBrands[selectModel]?.brand_name) }}
            </span>
            <q-icon v-else name="icon-star-asterisk" />
          </q-avatar>
        </div>
        <div>
          <span v-if="selectedOrgBrands[selectModel]?.brand_name">
            {{ selectedOrgBrands[selectModel]?.brand_name }}
          </span>
          <span v-else class="text-placeholder q-pr-sm">{{ placeholder }}</span>
        </div>
        <div
          v-if="
            brandCategories?.[selectedOrgBrands[selectModel]?.brand_type]?.label
          "
        >
          <q-badge
            class="badge"
            :class="{
              'badge-bg-disable':
                !brandCategories?.[selectedOrgBrands[selectModel]?.brand_type]
                  ?.bg_color,
              'badge-text-disable':
                !brandCategories?.[selectedOrgBrands[selectModel]?.brand_type]
                  ?.color,
            }"
            :style="{
              color:
                brandCategories?.[selectedOrgBrands[selectModel]?.brand_type]
                  ?.color,
              background:
                brandCategories?.[selectedOrgBrands[selectModel]?.brand_type]
                  ?.bg_color,
            }"
          >
            {{
              brandCategories?.[selectedOrgBrands[selectModel]?.brand_type]
                ?.label
            }}
          </q-badge>
        </div>
      </div>
    </template>

    <!-- Add New Brand -->
    <q-dialog
      persistent
      v-model="addBrand"
      transition-show="scale"
      transition-hide="scale"
      :transition-duration="300"
    >
      <CreateNewBrand
        style="max-width: 90vw; width: 640px"
        @added="
          (id) => {
            // Set Selected Task
            $emit('brand-added', id);
          }
        "
        @updated="
          (id) => {
            $emit('brand-updated', id);
          }
        "
        @finished="
          (id) => {
            addBrand = false;
            // Finished
            $emit('brand-finished', id);
          }
        "
        @close="
          () => {
            addBrand = false;
            // Set Selected Task
            $emit('brand-form-closed');
          }
        "
      />
    </q-dialog>
  </q-select>
</template>

<script>
import { ref, defineComponent } from 'vue';
import CreateNewBrand from 'src/components/Brands/CreateNewBrand.vue';
import { mapState, mapActions } from 'pinia';
import useGrowmodo from 'stores/growmodo';
import useUser from 'stores/user';
import useHelpers from 'stores/helpers';
import {
  searchInObjectArray,
  avatarNameAbbr,
} from 'src/assets/scripts/functions';

export default defineComponent({
  name: 'SelectBrand',

  components: { CreateNewBrand },

  emits: [
    'brand-added',
    'brand-updated',
    'brand-finished',
    'brand-form-closed',
  ],

  props: {
    placeholder: {
      type: String,
      default: 'Select or add a brand',
    },
    required: {
      type: Boolean,
      default: false,
    },
    initModel: {
      type: [String, Number],
      default: '',
    },
    dense: {
      type: Boolean,
      default: true,
    },
  },

  data() {
    return {
      filteredBrandsOptions: [],
      selectModel: null,
      inputModel: '',
      addBrand: false,
      avatars: ref({}),
      avatarNameAbbr,
    };
  },

  computed: {
    ...mapState(useUser, ['selectedOrgBrands']),
    ...mapState(useGrowmodo, ['brandCategories']),
  },

  watch: {
    initModel: {
      async handler(val) {
        if (val) this.selectModel = val;
      },
    },
    selectedOrgBrands: {
      async handler(val) {
        this.getBrandAvatars();
      },
      deep: true,
    },
  },

  mounted() {
    if (this.initModel) this.selectModel = this.initModel;
    this.getBrandAvatars();
  },

  methods: {
    ...mapActions(useHelpers, ['getSingleFile']),

    addNewBrand() {
      this.addBrand = true;
    },

    async getBrandAvatars() {
      Object.values(this.selectedOrgBrands).forEach(async (brand) => {
        const response = brand.avatar
          ? await this.getSingleFile(brand.avatar, true)
          : null;
        if (response?.success) this.avatars[brand.avatar] = response.data;
      });
    },

    filterBrandsFn(val, update) {
      update(() => {
        const newOpt = {
          id: 0,
          brand_name: 'Add New Brand',
          icon: 'icon-plus',
          action: () => this.addNewBrand(),
        };

        if (val === '') {
          this.filteredBrandsOptions = Object.values(
            this.selectedOrgBrands
          ).concat(newOpt);
        } else {
          const query_result = searchInObjectArray(
            this.filteredBrandsOptions.slice(0, -1),
            val,
            ['brand_name', 'website']
          );
          this.filteredBrandsOptions = query_result.concat(newOpt);
        }
      });
    },
  },
});
</script>
